import React, { useState } from 'react'
import { useParams } from 'react-router-dom'
import './style.css'
import {useDispatch, useSelector} from 'react-redux'
import { addItem, deleteReivew } from './store';
import styled from 'styled-components'


const Button = styled.button`

    width: calc(50% - 30px);
    margin-left: 20px;
    height: 50px;
    border: 1px solid #ccc;
    border-radius: 30px;
    margin-top: 60px;
    cursor: pointer;
    font-size: 18px;
    background-color:  #002053;
    color: #fff;
    transition: .3s;
    &:hover{
        background-color: #fff;
        color:#002053;
        box-shadow: 0 0 10px black;
        /* border: 1px solid #002053; */
    }
    &:nth-child(2){
        margin-right: 20px;
    }
 `

export default function DetailShoes(props) {
    const {shoes} = props
    const {id} = useParams()
    const dispatch = useDispatch()
    

    const [selectedOption, setSelectedOption] = useState('')
    const handleOptionChange = (event) => {setSelectedOption(event.target.value)}
    const colorChange = (optionValue) =>{
        if(selectedOption === optionValue){
        return {backgroundColor : '#002053', color:'#fff'};

        }
        return{};
    }

  //
  // const [userName, setUserName] = useState('') 
  // const [nameList, setNameList] = useState([])

  const [userDesc, setUserDesc] = useState('') //
  const [reviewList, setReviewList] = useState([])//



  let post = e =>{
    const copyReviewList = [...reviewList] //  

    copyReviewList.push(userDesc)//  

    setReviewList(copyReviewList) //    setreviewlist 

    setUserDesc('')// 


    // const copyNameList = [...nameList]
    // copyNameList.push(userName) 
    // setNameList(copyNameList)
    // setUserName('')
  }



  //
  const removeComment = (id) => {
    return setReviewList(reviewList.filter((comment) => comment.id == id)) ;
  };
  // !== console ,,,
  //console.log(reviewList.filter((comment) => comment.id !== id))


  //   // user.id id       
  //   // user.id === id  . true  ,,,

  // const onRemove = (id) => {
  //   let remove = reviewList.filter((review) => review.id !== id);
  //   setReviewList(remove)
  // };



    return (
    <div>
      <div className="detail_wrap">

        <div className="detail_left">
        <img src={shoes[id].image} alt="img" />
        </div>
        <div className="detail_right">
        <div className="detail_tit">{shoes[id].title}</div>
        <div className="detail_desc">{shoes[id].desc}</div>
        <div className="detail_price">{(shoes[id].price).toLocaleString('ko-KR')}</div>
        <div className="detail_option">

            <div className="opt_wrap">
            <label className='lab01' style={colorChange('230mm')}>
            <input
            className='opt01'
            name="radio_opt"
            type="radio"
            value="230mm"
            checked={selectedOption === '230mm'}
            onChange={handleOptionChange}
            />
            230
            </label>

            <label className='lab02' style={colorChange('250mm')}>
                <input
                className='opt02'
                name="radio_opt"
                type="radio"
                value="250mm"
                checked={selectedOption === '250mm'}
                onChange={handleOptionChange}
                />
                250
            </label>

            <label className='lab03' style={colorChange('260mm')}>
                <input
                className='opt03'
                name="radio_opt"
                type="radio"
                value="260mm"
                checked={selectedOption === '260mm'}
                onChange={handleOptionChange}
                />
                260
            </label>
            </div>

        </div>



        <Button onClick={()=>{dispatch(addItem({id: shoes[id].id, img: shoes[id].image, title: shoes[id].title, option: selectedOption,price : shoes[id].price ,count: 1}),alert(' .'))}}></Button>
        <Button onClick={()=>{dispatch(addItem({id: shoes[id].id, img: shoes[id].image, title: shoes[id].title, option: selectedOption,price : shoes[id].price ,count: 1}),alert(' .'))}}></Button>

        <div className="delivery_desc">
            <ul>
            <li></li>
            <li> CJ 1588-1255</li>
            <li>     3~5   .</li>
            <li>     []    .</li>
            <li>          .</li>
            <li>  (    .)</li>
            <li>       .</li>

            </ul>
        </div>

        </div>






        </div>

        <div className="category_tit">
        <h2>Review</h2>
        </div>
        <div className="review_wrap">

        <div className="review_box_img">
        <img src={shoes[id].image} alt="img" />
        </div>

        <div className="review_box">

        <div className="review_tit">
            <input className="title_input"
            type='text'
                placeholder=''
                name='title'
            // onChange={e=>{
            //   setUserName(e.target.value)
            // }}
            readOnly
            value={shoes[id].title}
            />
        </div>

        <div className="review_desc">
            <textarea className="desc_input"
                type='text'
                placeholder=' '
                name='desc'
                onChange={e=>{
                setUserDesc(e.target.value)
                }}
            
            
                value={userDesc}

                
            />

        </div>

        <button className={userDesc.length > 0 ? 'review_submit' : 'btn_disable'} onClick={post} disabled={userDesc.length > 0 ? false : true} ></button>



        </div>

        

        </div>

        


        <ul className='review_list_box'>

        {/* {
        nameList.map((name,idx)=>{
            return(
            <div key={idx}>
                <p>{name}</p>
            </div>
            )
        })
        
        } */}
        {
        reviewList.map((review,idx)=>{
            return(
            <li key={idx}>
                {idx + 1}) {review}<span className='li_span' onClick={removeComment}>X</span>
                
            </li>
            )
        })
        }

        


        </ul>









    </div>
  )
}
